

.box {
    position: relative;
    display: inline-block;
    width: 90%;
    padding: 50px 00px;
    margin-top: 60px;  
    color: unset;
    background-color: rgba(255, 255, 255, 0.646);
    border-radius: 5px;
    /* clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 90%); */
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.454);
    border-radius: 5px; 
  }
  
  .box::after {
    content: "";
    border-radius: 5px;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3); 
  } 

  /* ====================== */

  .probox {  
    padding: 10px;
    margin-top: 20px ; 
    -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  }
   
  .probox::after {  
    content: "";
    border-radius: 5px;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    opacity: 0;  
    -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  }
  
  .probox:hover {
    -webkit-transform: scale(1.05, 1.05);
    transform: scale(1.05, 1.05);
  }
  
  .probox:hover::after {
      opacity: 1;
  }  



