.box {
    position: relative;
    display: inline-block;
    width: 90%; 
    padding: 80px 00px;
    margin-top: 60px ;
    background-color: rgba(255, 255, 255, 0.646);
    border-radius: 5px;
    /* clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 90%); */
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.285);
    border-radius: 5px; 
  }

  
  /* ---------  */

.box>img{ 
    margin-left: 37%;
    /* border: 2px solid black; */
}

.h3{ 
    font-size: 35px;
    margin: 10px 0; 
}

.h1{
    margin: 10px 0; 
    font-size: 60px;
}

.h4{
    width: 70%; 
    font-size: large;
    margin: 20px auto; 
    color: rgb(21, 21, 21);
    line-height: 30px; 
    font-weight: bolder;
}


