
.box {
    position: relative;
    width: 90%; 
    padding: 50px 00px;
    margin-top: 60px ;
    background-color: rgba(255, 255, 255, 0.646);
    border-radius: 5px; 
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.454);
    border-radius: 5px;  
  }
   
  .box::after {
    content: "";
    border-radius: 5px;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3); 
  } 
 


  @media screen and (min-width:100px) and (max-width:920) {
    .box{ 
        display: grid;  
        border: 3px solid blue; 
    }
    
  }



